import React from 'react';
import GridViewIcon from '@mui/icons-material/GridView';
import ImageIcon from '@mui/icons-material/Image';
import FormatColorTextIcon from '@mui/icons-material/FormatColorText';
import InterestsIcon from '@mui/icons-material/Interests';
import Tabs from 'components/common/Tabs';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';

export type Props = {
  selectedTab: '' | '' | '' | '' | '';
  onSelect: (tab: Props['selectedTab']) => void;
};

function AssetTab({ selectedTab, onSelect }: Props) {
  const items = [
    { label: '', icon: <ImageIcon /> } as const,
    { label: '', icon: <GridViewIcon /> } as const,
    { label: '', icon: <FormatColorTextIcon /> } as const,
    { label: '', icon: <InterestsIcon /> } as const,
    { label: '', icon: <CloudUploadIcon /> } as const,
  ];
  const index = items.findIndex(item => item.label === selectedTab);

  return (
    <Tabs
      items={items}
      selectedIndex={index}
      orientation="vertical"
      onSelect={index => onSelect(items[index].label)}
    />
  );
}

export default AssetTab;
